import Default from "../default/Default"
import Find from "../find/Find"
export default {
	data() {
		return {
			val:"",
			show:true,//控制搜索放大镜图片显示隐藏
			hide:false,//控制搜索前页面显示
			hideh:true,//控制搜索后页面显示
			statusBarHeight:0//状态栏高度
		}
	},
	components:{
		Default,Find
	},
	methods:{
		inptu(e){
			if(e.detail.value!=""){
				this.show=false
			}else{
				this.show=true
			}
			this.val = e.detail.value
		},
		search(){
			let that = this
			uni.request({
				url:"http://ceshi3.dishait.cn/api/goods/search",
				data:{
					title:that.val,
					page:1,
					all:"asc",
					price:"between"
				},
				method:"POST",
				success(res) {
					console.log(res)
				}
			})
		}
	},
	created() {
		let that = this
		uni.getSystemInfo({
			success(res) {
				that.statusBarHeight = res.statusBarHeight+10//状态栏高度+10
			}
		})
		
		
	}
}
